@use '../../base/02-tokens/maps' as maps;

@mixin eui-compact-get-tokens($ratio: 0.875) {
    --eui-compact-ratio: #{$ratio};
    @each $spacing, $value in maps.$spacing-map {
        --eui-s-#{$spacing}: calc( #{var(--eui-compact-ratio)} * #{$value} );
    }
}

// GLOBAL COMPACT THEME
///////////////////////
html.eui-t-compact.eui-19 {
    --eui-f-size-base: 14px !important;              // Font size base for compact theme
    font-size: var(--eui-f-size-base);               // Applies the defined custom font size

    // CHECKBOXES
    .eui-input-checkbox:not(.cdk-visually-hidden) {
        // Default user-agent checkbox customization & alignements
        height: var(--eui-s-l);
        width: var(--eui-s-l);

        // CHECKED
        &:checked {
            &::before {
                height: var(--eui-s-l);
                width: var(--eui-s-l);
            }
        }

        // INDETERMINATE
        &:indeterminate {
            &::before {
                height: var(--eui-s-xl);
                width: var(--eui-s-xl);
            }
        }

        // DISABLED
        &:disabled:not([readonly]) {
            &:checked {
                &::before {
                    height: var(--eui-s-xl);
                    width: var(--eui-s-xl);
                }
            }
        }

        // READONLY
        &[readonly] {
            &:checked {
                &::before {
                    height: var(--eui-s-xl);
                    width: var(--eui-s-xl);
                }
            }
        }

        // INVALID
        &.eui-input-checkbox--invalid:not(:disabled):not([readonly]),
        &.eui-input-checkbox--danger:not(:disabled):not([readonly]) {
            &:checked {
                &::before {
                    height: var(--eui-s-xl);
                    width: var(--eui-s-xl);
                }
            }
        }
    }

    // RADIOS
    .eui-input-radio:not(.cdk-visually-hidden) {
        height: var(--eui-s-l);        // 20x
        width: var(--eui-s-l);         // 20px

        // CHECKED
        &:checked {
            &::before {
                height: var(--eui-s-m);
                width: var(--eui-s-m);
            }
        }

        // DISABLED
        &:disabled:not([readonly]) {
            &:checked {
                &::before {
                    height: var(--eui-s-m);
                    width: var(--eui-s-m);
                }
            }
        }

        // INVALID
        &.eui-input-radio--invalid,
        &.eui-input-radio--danger {
            &:checked {
                &::before {
                    height: var(--eui-s-m);
                    width: var(--eui-s-m);
                }
            }
        }
    }
}

// FORM ELEMENTS
/////////////////
form.eui-t-form-compact {
    --eui-f-size-base: 14px !important;         // Font size base for compact theme
    --eui-input-group-spacing: var(--eui-s-s);  // Spacing between form groups elements
    // Internals
    // See: eui-styles\src\base\02-base\mixins\theming\_compact-vars.scss
    @include eui-compact-get-tokens();
    // Setup
    --eui-compact-ratio: 0.82;                  // Sets form inputs/elements to 39px height (default: 44px)
    font-size: var(--eui-f-size-base);          // Applies the defined custom font size

    // Internals
    .eui-input-group {
        margin-bottom: var(--eui-input-group-spacing);

        label {
            font-size: var(--eui-f-size-base) !important;
        }
    }

    .eui-input-text,
    .eui-input-number {
        padding: calc(var(--eui-s-xs) - 1px);
    }

    .eui-button--icon-only {
        padding: calc(#{var(--eui-s-xs)} + 1px);

        .eui-icon-svg svg {
            height: var(--eui-is-s);
            width: var(--eui-is-s);
        }
    }
}

// TABLE
/////////////////
.eui-table.eui-t-table-compact {
    // Exposed CSS variables
    --eui-f-size-base: 14px !important;           // Font size base for compact theme
    --eui-table-header-padding: var(--eui-s-s);   // Table header spacer set to 0.75rem
    --eui-table-row-padding: var(--eui-s-2xs);    // Table body row spacer set to 0.25rem
    --eui-table-footer-padding: var(--eui-s-s);   // Table footer spacer set to 0.75rem
    // Internals
    @include eui-compact-get-tokens();
    // Setup
    font-size: var(--eui-f-size-base);            // Applies the defined custom font size

    // Internals
    thead {
        tr {
            height: auto;

            th {
                padding: var(--eui-table-header-padding)  var(--eui-s-s);
                font-size: var(--eui-f-size-base);
            }
        }
    }

    tbody {
        tr {
            height: auto;

            td {
                padding: var(--eui-table-row-padding)  var(--eui-s-s);
            }
        }
    }

    tfoot {
        tr {
            height: auto;

            td {
                padding: var(--eui-table-footer-padding)  var(--eui-s-s);
            }
        }
    }
}

// CARD
/////////////////
.eui-card.eui-t-card-compact {
    // Exposed CSS variables
    --eui-f-size-base: 14px !important;              // Font size base for compact theme
    --eui-card-title-size: var(--eui-f-size-m);      // Card title font size
    --eui-card-subtitle-size: var(--eui-f-size-s);   // Card subtitle font size
    // Internals
    @include eui-compact-get-tokens();
    // Setup
    font-size: var(--eui-f-size-base);                    // Applies the defined custom font size

    // Internals
    .eui-card {
        &-header {
            &__title-container {
                &-title,
                &-title .eui-label {
                    font-size: var(--eui-card-title-size);
                }

                &-subtitle,
                &-subtitle .eui-label {
                    font-size: var(--eui-card-subtitle-size);
                }
            }

            &__urgent-indicator {
                &::after {
                    font-size: var(--eui-f-size-2xl);
                }
            }
        }
    }
}
